/* layout */
@import 'stars.scss';

.wrap {
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  padding: 0;
  display: flex;
  margin: 0;
  height: 100vh;
}

main {
  padding: 1rem;
  z-index: 1;
  width: 100%;
}

@media (min-width: 370px) {
  main {
    padding: 0;
  }
}

@media (min-width: 370px) {
  main nav ul {
    display: block;
  }
}

html {
  height: 100%;
  cursor: url('../public/cursor.svg'), default;
  background: #002;
  background: radial-gradient(ellipse at bottom, #204 0%, #002 80%);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

*,
a:hover {
  cursor: url('../public/cursor.svg'), default;
}

a {
  transition: 0.5s ease;
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #207 #001;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 1rem;
}

*::-webkit-scrollbar-track {
  background: #001;
}

*::-webkit-scrollbar-thumb {
  background-color: #ddeeff;
  border-radius: 1rem;
  border: 0rem none #001;
}

.loading {
  transition: 0.2s ease;
  opacity: 0;
  z-index: -1;
  position: absolute;
  overflow: hidden;
}

.loaded {
  transition: 0.2s ease;
  opacity: 1;
  z-index: 1;
  position: relative;
  overflow: unset;
}